<template>
  <div class="">
    <div class="topMenu"></div>
    <div style="background: white;border-bottom: 1px solid #EEEEEE;padding: 20px 0;">
      <div class="topBox">
        <div>
          <span></span>
          <el-select v-model="course">
            <el-option
              v-for="(item,index) in courses"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </div>
        <div>
          <span></span>
          <el-select v-model="teacTime">
            <el-option
              v-for="(item,index) in teacTimes"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </div>
        <div>
          <span></span>
          <el-select v-model="taskName">
            <el-option
              v-for="(item,index) in taskNames"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="line"></div>
      <div class="claMsgBox">
        <div>
          <div><span>{{claMsgData.kechen}}</span></div>
          <div><span>{{claMsgData.banji}}</span></div>
        </div>
        <div>
          <div><span>{{claMsgData.beginT}}</span></div>
          <div><span>{{claMsgData.endT}}</span></div>
        </div>
        <div>
          <div><span>{{claMsgData.missCount}}</span></div>
          <div><span>{{claMsgData.state}}</span></div>
        </div>
      </div>
      <p style="width:960px;margin:0 auto;font-size:14px;color:rgba(0,0,0,.65)">
        <span>{{claMsgData.text}}</span>
      </p>
      <div class="line"></div>
      <div style="position: relative">
        <div style="width:100%;height:9px;background:#F8F8F8;position:absolute;top: 40px"></div>
        <div class="sxtabBox">
          <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane name="first">
              <span slot="label"><i class="el-icon-menu" style="margin-right: 8px"></i></span>
              <div class="tabContentBox">
                <p><span class="light">30</span></p>
                <ul class="chengjiList">
                  <li>
                    <span class="xm"></span>
                    <span class="xh"></span>
                    <span class="bj"></span>
                    <span class="defen"></span>
                    <span class="rank"></span>
                    <span class="chaoz"></span>
                  </li>
                  <li v-for="(item,index) in chengjiData" :key="index">
                    <span class="xm">{{item.name}}</span>
                    <span class="xh">{{item.xuehao}}</span>
                    <span class="bj">{{item.banji}}</span>
                    <span class="defen">{{item.defen}}</span>
                    <span class="rank">{{item.rank}}</span>
                    <span class="chaoz light"><i class="el-icon-edit-outline"></i></span>
                  </li>
                </ul>
                <div class="pagein">
                  <el-pagination
                    background
                    layout="prev, pager, next"
                    :total="1000">
                  </el-pagination>
                </div>
              </div>
            </el-tab-pane>
            <el-tab-pane name="second">
              <span slot="label"><i class="el-icon-tickets" style="margin-right: 8px"></i></span>
              
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Shuju',
  data () {
    return {
      courses: ['',''],
      course: '',
      teacTimes: ['2017-2018','2017-2018'],
      teacTime: '2017-2018',
      taskNames: ['',''],
      taskName: '',
      claMsgData: {
        kechen: '',
        banji: '18',
        beginT: ' 2017-12-27 10:00',
        endT: ' 2017-12-27 10:00',
        missCount: '100',
        state: '',
        text: ',, ,,,,,,,',
      },
      activeName: 'first',
      chengjiData: [
        {
          name: '',
          xuehao: '1341301046',
          banji: '13',
          defen: 100,
          rank: 1,
        },{
          name: '',
          xuehao: '1341',
          banji: '13',
          defen: 100,
          rank: 1,
        },{
          name: '',
          xuehao: '1341301046',
          banji: '13',
          defen: 100,
          rank: 1,
        },
      ]
    }
  },
  mounted() {

  },
  methods: {
    handleClick() {

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .line {
    height: 1px;
    background-color: #EEEEEE;
    margin-top: 20px;
  }
  .topBox {
    width: 960px;
    margin: 0 auto;
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    margin-top: 10px;
  }
  .topBox>div>span {
    font-size: 14px;
    color: rgba(0, 0, 0, 0.45);
  }
  .claMsgBox {
    width: 960px;
    margin: 20px auto 10px auto;
  }
  .claMsgBox>div {
    display: inline-block;
    width: 200px;
    margin-right: 120px;
  }
  .claMsgBox>div:first-child, .claMsgBox>div:last-child {
    margin-right: 20px;
  }
  .claMsgBox>div div {
    font-size: 14px;
    line-height: 20px;
    color: rgba(0, 0, 0, .45);
  }
  .claMsgBox>div div span {
    color: rgba(0, 0, 0, .65);
  }
  .sxtabBox {
    width: 960px;
    margin: 0 auto;
    background-color: white;
    font-size: 14px;
  }
  .el-tabs__item {
    color: rgba(0,0,0,.65)
  }
  .tabContentBox {
    padding: 20px 0;
  }
  .tabContentBox p {
    color: #687178;
    font-size: 12px;
  }
  .tabContentBox p .light {
    color: #1890FF;
    margin: 0 3px;
  }
  .chengjiList {
    font-size: 14px;
    color: #687178;
  }
  .chengjiList li {
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    border-bottom: 1px solid #E8E8E8;
    line-height: 47px;
  }
  .chengjiList li:first-child {
    color: black;
    background-color: #F8F8F8;
    border-top-right-radius: 2px;
    border-top-left-radius: 2px;
  }
  .chengjiList li span {
    width: 0;
    flex-grow: 1;
    text-indent: 60px;
  }
  .chengjiList li span i {
    text-indent: 0;
  }
  .chengjiList li .light {
    cursor: pointer;
    color: #1890FF;
  }
  .pagein {
    margin: 45px auto 0px auto;
    text-align: right;
  }
</style>



