<template>
  <div class="">
    <div class="Account_top">
    	    <p style="color: #687178;font-size: 12px;padding-top:20px;padding-bottom:10px;"></p>
    	    <el-tabs :tab-position="tabPosition">
				    <el-tab-pane label="">
				    	 <p class="title_t"> <span>30</span>30</p>
				    	   <div class="tea_table">
				    	   	 <div class="tea_table_top">
				    	   	     <ul>
				    	   	     	<li
				    	   	     	v-for="(itemdata,index) in data" 
				    	   	     	:key="index">
							          {{itemdata}}</li>
				    	   	     </ul>
				    	   	 </div>
				    	   	 <div class="tea_tables">
				    	       <table>
							    <tbody class="tabod">
							       <tr 
							       	v-for="(itemgridData,index) in gridData"
							       	:key="index">
							        <td  v-for="(item,index) in itemgridData">
							        	<i v-if="index == 'degree'" class="el-icon"></i>
							        	{{item}}
							        </td>
							      </tr>
							    </tbody>
							  </table>
							 </div>
				    	</div>
				    </el-tab-pane>
				    <el-tab-pane label="">
				    	 	 <p class="title_t"> <span>30</span><span>300</span><span>100</span></p>
				    		<div class="tea_table">
				    	   	 <div class="tea_table_top">
				    	   	       <el-table
											    :data="gridData_student"
											    style="width: 100%">
											    <el-table-column
											      prop="id"
											      label=""
											      >
											    </el-table-column>
											    <el-table-column
											      prop="college"
											      label=""
											      sortable
											       width="180"
											      :filters="college"
											      :filter-method="filterHandler"
											    >
											    </el-table-column>
											    <el-table-column
											      prop="classs"
											      label=""
											      sortable
											      width="180"
											      :filters="classs"
											      :filter-method="filterHandler"
											    >
											    </el-table-column>
											    <el-table-column
											      prop="tea_class"
											      label=""
											      >
											    </el-table-column>
											     <el-table-column
											      prop="name"
											      label=""
											      
											      >
											    </el-table-column>
											    <el-table-column
											      prop="when"
											      label=""
											      width="100">
											    </el-table-column>
											    <el-table-column
											      prop="kaitime"
											      label=""
											       width="180" >
											    </el-table-column>
											    <el-table-column
											      prop="endtime"
											      label=""
											      width="180">
											    </el-table-column>
											    <el-table-column
											      prop="degree"
											      label=""
											      width="120"
											      :filters="[{ text: '', value: '' }, { text: '', value: '' }]"
											      :filter-method="filterTag"
											      filter-placement="bottom-end">
											      <template slot-scope="scope">
											        <el-tag
											          :type="scope.row.degree === '' ? 'primary' : 'success'"
											          close-transition>{{scope.row.degree}}</el-tag>
											      </template>
											    </el-table-column>
											  </el-table>
				    	   	 </div>
				    	</div>
				    </el-tab-pane>
				  </el-tabs>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Account',
  data() {
      return {
        college:[{text: '', value: ''}, {text: '', value: ''}, {text: '', value: ''}],
        classs:[{text: '2015', value: '2015'}, {text: '2016', value: '2016'}, {text: '2017', value: '2017'}, {text: '2018', value: '2018'}],
        tabPosition: 'top',
        data:["","","","","","","",""],
        gridData: [
	      { id:'01',name: '18524550255', teacher: '',when:'2017-20181', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'02',name: '18524550255', teacher: '1',when:'2017-2018', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'03',name: '18524550255', teacher: '2',when:'2017-20182', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'04',name: '18524550255', teacher: '3',when:'2017-2018', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'05',name: '18524550255', teacher: '4',when:'2017-2018', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'06',name: '18524550255', teacher: '',when:'2017-20183', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'06',name: '18524550255', teacher: '',when:'2017-20184', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'06',name: '18524550255', teacher: '',when:'2017-2018', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'06',name: '18524550255', teacher: '',when:'2017-2018', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'06',name: '18524550255', teacher: '',when:'2017-2018', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'06',name: '18524550255', teacher: '',when:'2017-2018', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'06',name: '18524550255', teacher: '',when:'2017-2018', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'06',name: '18524550255', teacher: '',when:'2017-2018', college: '',classs:'10101010',tea_class:'',degree:''},
	      { id:'06',name: '18524550255', teacher: '',when:'2017-2018', college: '',classs:'10101010',tea_class:'',degree:''},
      ],
			gridData_student: [
		      { id:'01',college: '',classs:'2015',tea_class:'1701', name:'',when:'20142014',kaitime:'2015-01-02 00:00:00',endtime:'2015-01-02 00:00:00',degree:''},
		      { id:'02',college: '',classs:'2016',tea_class:'1702', name:'',when:'20142014',kaitime:'2015-01-02 00:00:00',endtime:'2015-01-02 00:00:00',degree:''},
		      { id:'03',college: '',classs:'2017',tea_class:'1703', name:'',when:'20142014',kaitime:'2015-01-02 00:00:00',endtime:'2015-01-02 00:00:00',degree:''},
	      ],
        value: ''
      }
   },
   methods: {
      formatter(row, column) {
        return row.address;
      },
      filterTag(value, row) {
        return row.degree === value;
      },
      filterHandler(value, row, column) {
        const property = column['property'];
        return row[property] === value;
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.Account_top{
	min-height: 500px;
	background-color: white;
	padding-left: 25px;
	margin-top: 5px;
}
.title_t{
	height:20px; 
	font-size:12px;
	font-family:PingFangSC-Regular;
	color:rgba(104,113,120,1);
	line-height:20px;
}
.title_t>span{
  display: inline-block;
	color:#1890FF;
	padding: 0px 5px;
}
.tea_table{
   margin-top: 16px;
   font-size:12px;
   font-family:PingFangSC-Regular;
   color:rgba(104,113,120,1);
   margin-right: 25px;
}
.tea_table thead{
    height: 48px;
}
.tea_table_top ul{
	display: flex;
	height:48px; 
	background:rgba(248,248,248,1);
	box-shadow: 0px -1px 0px 0px rgba(232,232,232,1);
	border-radius: 2px 2px 0px 0px ; 
	margin-bottom: 0px;
}
ul{
	-webkit-padding-start: 0px;
}
.tea_table_top ul>li{
	text-align: center;
	height: 48px;
	line-height: 48px;
	width: 100%;
}
.tea_tables{
	height: 500px;
	overflow-y: scroll;
}
.tabod>tr>td:nth-child(4){
	width: 17%;
}
.tea_table_top ul>li:nth-child(4){
	width: 153%;
}
.tabod>tr>td:nth-child(3){
	padding-left: 0.5%;
}
.tabod>tr>td:nth-child(5){
	padding-left: 0.5%;
}
.tea_table_top ul>li:nth-child(5){
	width: 100%;
}
.tabod>tr>td{
	text-align: left;
  width: 11.3%;
  position: relative;
}
.tabod>tr{
	height: 48px;
	line-height: 48px;
	cursor: pointer;
}
.tabod>tr:hover{
	cursor: pointer;
	background-color: rgba(21,124,240,0.4);
	color: white;
}

.el-icon{
	width: 5px;
	height:5px;
	border-radius: 50%;
	background-color: #00B0FF;
	position: absolute; 
  left: -10%;
  margin-top: 19px;
}
.el-icon_in{
	width: 5px;
	height:5px;
	border-radius: 50%;
	background:rgba(245,166,35,1);
	position: absolute; 
  left: -10%;
  margin-top: 19px;
}
table{ 
    border-collapse:collapse;
    width: 100%;    
}
.tea_table_top ul{
	display: flex;
	height:48px; 
	background:rgba(248,248,248,1);
	box-shadow: 0px -1px 0px 0px rgba(232,232,232,1);
	border-radius: 2px 2px 0px 0px ; 
	margin-bottom: 0px;
}
ul{
	-webkit-padding-start: 0px;
}
.tea_table_top ul>li{
	text-align: left;
	height: 48px;
	line-height: 48px;
	width: 100%;
}
</style>



